* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.appWrapper{
  display: grid;
  grid-template-areas:
  "header header header";
  grid-template-rows: 100%;
  grid-template-columns: 100%;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
}

.waiting {
  text-align: center;
  padding-top: 7em;
  padding-bottom: 2em;
}
.navTop{
    grid-area: header;
    display: grid;
        display: -ms-grid;
    grid-template-rows: 100%;
    grid-template-columns: 20% 1fr 50%; /* 20% 1fr 50%  // 0% 1fr 0% для снега в меню*/
        -ms-grid-rows: 100%;
        -ms-grid-columns: 20% 1fr 80%; /* 20% 1fr 80%  // 100% для снега в меню*/
    position: absolute;
    width: 100%;
    height: 5em;
    padding: 10px 20px; /* 10px 20px // 0px 20px для снега в меню*/
    background: -webkit-gradient(linear, left top, left bottom, from(rgb(28, 58, 73)), to(#11141b));
    background: linear-gradient(to bottom, rgb(28, 58, 73), #11141b);
    align-items: center;
    opacity: 0.9;
    z-index: 5;
    -webkit-transition: 0.8s ease;
    transition: 0.8s ease;
    /* animation: disapear 0.5s ease; */
}

#snow {
    width: 100%;
    height: 100%;
    background-image: url("https://yraaa.ru/_pu/27/15825154.png"), url("https://yraaa.ru/_pu/27/89961317.png"), url("https://yraaa.ru/_pu/27/21791255.png");
        -webkit-animation: snow 20s linear infinite;
    animation: snow 20s linear infinite;
}

/* Анимация Новогодний снег ======================================================================================================================= */
/* @keyframes snow {0% {background-position: 0px 0px, 0px 0px, 0px 0px;} 100% {background-position: 500px 1000px, 400px 400px, 300px 300px;}}
@-moz-keyframes snow {0% {background-position: 0px 0px, 0px 0px, 0px 0px;} 100% {background-position: 500px 1000px, 400px 400px, 300px 300px;}}
@-webkit-keyframes snow {0% {background-position: 0px 0px, 0px 0px, 0px 0px;} 100% {background-position: 500px 1000px, 400px 400px, 300px 300px;}}
@-ms-keyframes snow {0% {background-position: 0px 0px, 0px 0px, 0px 0px;} 100% {background-position: 500px 1000px, 400px 400px, 300px 300px;}} */

@keyframes disapear {0% {opacity: 0;} 100% {opacity: 1;}}
@-webkit-keyframes disapear {0% {opacity: 0;} 100% {opacity: 1;}}

/* @keyframes moveOutmain{
    100%{transform: translateY(0px);}
    0%{transform: translateY(-10px);}
} */

.scrolled{
    position: fixed;
    display: grid;
        display: -ms-grid;
    grid-template-rows: 100%;
    grid-template-columns: 20% 1fr 80%; /* 20% 1fr 80%  // 0% 1fr 0% для снега в меню*/
        display: -ms-flexbox;
        /* -ms-grid-rows: 100%;
        -ms-grid-columns: 20% 1fr 80%; */
    width: 100%;
    /* height: 2em; */
    height: 44px;
    padding: 10px 20px;
    background: -webkit-gradient(linear, left top, left bottom, from(rgb(28, 58, 73)), to(#11141b));
    background: linear-gradient(to bottom, rgb(28, 58, 73), #11141b);
    /* background: gray; */
    align-items: center;
    z-index: 5;
    -webkit-transition: 0.8s ease;
    transition: 0.8s ease;
    opacity: 0.9;
    -webkit-animation: moveOut 0.8s ease;
            animation: moveOut 0.8s ease;
}

@keyframes moveOut{ 0%{ -webkit-transform: translateY(-44px); transform: translateY(-44px); height: 0px; } 100%{-webkit-transform: translateY(0px);transform: translateY(0px); height: 44px; } }
@-webkit-keyframes moveOut{ 0%{ -webkit-transform: translateY(-44px); transform: translateY(-44px); height: 0px; } 100%{-webkit-transform: translateY(0px);transform: translateY(0px); height: 44px; } }


